import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

//Routing Module
import { AppRouting } from './routing/app.routing';

//Application Components
import { AppComponent } from './app.component';
import { AppNevBar } from './component/app-nev-bar/app-nev-bar.component';
import { AppImgSlider } from './component/app-img-slider/app-img-slider.component';
import { AppContactusComponent } from './component/app-contactus/app-contactus.component';
import { AppFooterComponent } from './component/app-footer/app-footer.component';
import { AppArtistInfoComponent } from './component/app-artist-info/app-artist-info.component';
import { AppPageHederTextComponent } from './component/app-page-heder-text/app-page-heder-text.component';

@NgModule({
  declarations: [
    AppComponent,AppNevBar,AppImgSlider, AppContactusComponent, AppFooterComponent, AppArtistInfoComponent, AppPageHederTextComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRouting
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



