export class AppPageHeaderTextService{
    pageHeader:String="";
    setDynamicPageHeader(pageHeader:String)
    {
        if (pageHeader!=null && pageHeader!="")
        this.pageHeader=pageHeader;
        else
        this.pageHeader="Page Under Construction...";
    }
}


