import { Component, OnInit,Input } from '@angular/core';
import {AppPageHeaderTextService} from '../../services/app-page-header-text.service';

@Component({
  selector: 'app-page-heder-text',
  templateUrl: './app-page-heder-text.component.html',
  styleUrls: ['./app-page-heder-text.component.css'],
  providers:[AppPageHeaderTextService]
})
export class AppPageHederTextComponent implements OnInit {

@Input() pageHeader: String ;
  constructor(private appPageHeaderTextService :AppPageHeaderTextService ) { }

  ngOnInit() {
    this.appPageHeaderTextService.setDynamicPageHeader(this.pageHeader);
  }

}



