import { NgModule } from '@angular/core';
import { RouterModule,Routes } from '@angular/router';

import { AppImgSlider } from '../component/app-img-slider/app-img-slider.component';
import { AppArtistInfoComponent } from '../component/app-artist-info/app-artist-info.component';

const routing : Routes = [
    {path:'',redirectTo:'/Home',pathMatch:'full'},
    {path:'Home',component: AppImgSlider},
    {path:'Gallery',component: AppImgSlider},
    {path:'Artist',component: AppArtistInfoComponent},
];

export const AppRouting = RouterModule.forRoot(routing);


